.ju-process-bar {
  position: fixed;
  top: 0;
  left: -100%;
  z-index: 70;
  width: 100%;
  height: 3px;
  background: #FE5722;
  animation: loading-anim 1s ease-in-out infinite; }

@-webkit-keyframes loading-anim {
  to {
    transform: translateX(200%); } }

@keyframes loading-anim {
  to {
    transform: translateX(200%); } }
